/* Center website */
.main {
  max-width: 1000px;
  margin: auto;
}

.row {
  margin: auto;
  text-align: center;
}

/* Add padding BETWEEN each column */
.row,
.row > .column {
  padding: 1.5em;
}

/* Create four equal columns that floats next to each other */
.column {
  display:inline-block;
}

/* Clear floats after rows */ 
.row:after {
  content: "";
  display: table;
  clear: both;
}

.inline { float:left; }

.portfolioImageContainer {
  position: relative;
  max-height:20vw;
  max-width:20vw;
  min-width:10em;
  min-height:10em;
}

.portfolioImage{
  display:block;
  max-height:20vw;
  max-width:20vw;
  min-width:10em;
  min-height:10em;
  vertical-align:top;
}

.portfolioImage:hover{
  
}

.portfolioOverlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  line-height:100%;
  vertical-align:top;
  cursor:pointer;
  transition: .6s ease;
  background-color: rgba(40,40,40,.7);
}

.portfolioImageContainer:hover .portfolioOverlay {
  opacity: 1;
}

.portfolioImageText {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  line-height:1.6;
  vertical-align:top;
  transform: translate(-50%, -50%);
  text-align: center;
}


/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 900px) {
  .portfolioImage{
    max-width:40vw;
    max-height:40vw; 
  }

  .portfolioImageContainer {
    max-height:40vw;
    max-width:40vw;
  }

  .column {
    /*width: 50%;*/
  }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .portfolioImage{
    max-width:80vw;
    max-height:80vh; 
  }

  .portfolioImageContainer {
    position: relative;
    max-height:80vw;
    max-width:80vw;
  }

  .column {
    /*width: 100%;*/
  }
}

/* The Modal (background) */
.modal {
  position: fixed; /* Stay in place */
  z-index: 1000; /* Sit on top */
  padding-top: 10px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  line-height:100%;
  overflow: hidden; /* scrolling is currently disabled*/
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,.9); /* Black w/ opacity */
}

.fadeOut{
  opacity:0;
  width:0;
  height:0;
  transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;

}

.fadeIn{
  opacity:1;
  width:100%;
  height:100%;
  transition: opacity .5s;

}

